import FormData from 'form-data'
export default {
  name: "MyDashboard",
  middleware: 'auth',
  data() {
    return {
      loading: false
    }
  },
  head() {
    return {
      title: 'Dasbor'
    }
  },
  computed: {
    auth() {
      return this.$store.getters.auth
    },
    links() {
      return [
        {
          text: 'Aktifitas',
          icon: 'activity',
          to: '/dashboard/me'
        },
        {
          text: 'Edit Profil',
          icon: 'settings',
          to: '/dashboard/me/edit'
        },
        {
          text: 'Diikuti',
          icon: 'user-check',
          to: '/dashboard/me/followings'
        },
        {
          text: 'Favorit',
          icon: 'heart',
          to: '/dashboard/me/favorites'
        },
      ]
    }
  },
  methods: {
    handleClick() {
      this.$refs.input.click()
    },
    async handleChange(ev) {
      this.loading = true
      try {
        const file = ev.target.files[0]
        const data = new FormData()
        data.append('avatar', file)
        const { url } = await this.$axios.$post('/account/avatar', data)
        this.$store.commit('auth/setAuth', { ...this.auth, avatar: url })
        this.$toast.success('Foto anda telah diupdate.')
      } catch (e) {
        this.$toast.danger(this.$errorMessage(e))
      }
      this.loading = false
    },
  },
}
